import './App.css';
import {Header} from './Header/Header';
import {China} from './ChinaFood/China';
import {Indian} from './IndianFood/Indian'
import { Italian } from './ItalianFood/Italian';
import { Footer } from './Footer/Footer';
import { Japan } from './JapanFood/Japan';
import { useState } from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap/dist/js/bootstrap.bundle'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.js';




let choose="Cartoon";
function selectSeries(){

 

  if(choose.includes('China')){
    return <China></China>
  }

  else if(choose.includes('Indian')){
    return <Indian></Indian>
  }

  else if(choose.includes('Japan')){
    return <Japan></Japan>
  }
  else if(choose.includes('Italian')){
    return <Italian></Italian>
  }
  else if(choose.includes('All')){
    return <>
    <China></China>
    <Indian></Indian>
    <Japan></Japan>
    <Italian></Italian>
  </>
  }

  else{
    return <>
    <China></China>
    <Indian></Indian>
    <Japan></Japan>
    <Italian></Italian>
    </>
  }
}


function App(){
  let [choose1, setOption]=useState('All');
  choose=choose1;

  function chOption(event){
    setOption(event.target.value);
    choose=choose1;
  }

  return (
<>
  <header className='header'>
  <label style={{'color':'blue','fontSize':'20px'}}>Select Option: </label>
    <select value={choose1} onChange={chOption}>
      <option value="China">China Food</option>
      <option value="Indian">Indian Food</option>
      <option value="Japan">Japan Food</option>
      <option value="Italian">Italian Food</option>
      <option value="All">All Food</option>
    </select>
  </header>

    <div>
    <Header></Header>
    {selectSeries()}
    <Footer></Footer>
    </div>
    </>
  );
}

export default App;
