import './FoodCard.css'

let height = 150;
let width = 300;


export function FoodCard(props) {


    return (
        <div className="cardbox">
            <img src={props.imgPath} height={height} width={width}></img>
            <figcaption className='title'>{props.title}</figcaption>
            <figcaption className='price'>₹{props.price}</figcaption>
            <a className='buy' href={props.myLink}>Buy Now</a>
        </div>

    )
}